@charset "utf-8";

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.space-between {
  justify-content: space-between;
}

section {
  padding: 60px 0;
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Header                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
header {
  height: 60px;
  background-color: #5c178d;
}

h1, .header-list a:hover {
  color: #fff;
}

header h1 {
  font-size: 2.2rem;
  font-weight: 500;
  text-transform: uppercase;
  line-height: 60px;
  letter-spacing: 0.2px;
}

.header-list li {
  cursor: pointer;
  margin-right: 25px;
  line-height: 60px;
  font-size: 18px;
  font-weight: 500;
}

a {
  text-decoration: none;
}

.header-list a {
  color: rgb(241 227 250);
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Banner                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44rem;
  background: #f8f9fe url('https://cdn.dribbble.com/users/72506/screenshots/17150636/media/03d39dee8956cbef2f44af459702053a.jpg?resize=1200x900&vertical=center') center 60% / cover no-repeat;
}

main .banner::before {
  position: absolute;
  top: 60px;
  left: 0;
  height: 57%;
  width: 100%;
  background-color: rgb(0 0 0 / 0.42);
  content: '';
 }

.banner .container {
  position: relative;
  z-index: 99999;
  text-align: center;
}

.banner h1 {
  font-size: 4.4rem;
  font-weight: 600;
}

.banner-text {
  width: 75%;
  margin: 0 auto;
}

.banner p {
  padding: 20px 0 50px;
  font-size: 1.8rem;
  text-align: center;
  color: #fff;
}

/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Gallery                                              */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
.purple {
  color: rgb(112 33 168);
}

.gallery h2, .gallery p {
  text-align: center;
}

.gallery h2 {
  font-size: 2.8rem;
  font-weight: 600;
  color: rgb(36 32 45);
}

.gallery p {
  font-size: 16px;
  font-weight: 400;
  color: rgb(81 76 91);
}

.movie-list {
  margin: 40px 0;
  gap: 26px;
}

.movie-list img {
  width: auto;
  border-radius: 5px;
  box-shadow: 1px 1px 4px rgb(189 187 193 / 0.45);
  transition: all 0.25s ease-in-out;
  cursor: pointer;
}

.movie-list img:hover {
  transform: translateY(-4px);
}

.movie-list p {
  padding-top: 8px;
  font-size: 18px;
  font-weight: 500;
  color: rgb(36 32 45);
}


/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
/*  Footer                                               */
/* - - - - - - - - - - - - - - - - - - - - - - - - - - - */
footer {
  background-color: #0f0417;
  font-size: 1.8rem;
  font-weight: 400;
  color: #fff;
  line-height: 60px;
  text-align: center;
}

footer p {
  padding-bottom: 30px;
}

.footer-row ul {
  padding: 30px 0 0;
  justify-content: center;
  gap: 25px;
}

footer a {
  color: #b873ea;
}

footer a:hover {
  color: #fff;
}